import * as React from "react";
import ReactDOM from "react-dom";
import {Map, TileLayer, Marker, Popup} from "react-leaflet";
import l from "leaflet";
import {
    Card,
    Button,
    CardTitle,
    CardText,
    Row,
    Col,
    Form,
    FormControl,
    FormGroup,
    ControlLabel,
    Label,
    Input,
} from "reactstrap";

import Parser from "html-react-parser";

import HelloWorld from "./components/hello";
import "../app.css";
import "leaflet/dist/leaflet.css";

const myIcon = L.icon({
    iconUrl:
        "data:image/png;base64,",
    iconSize: [25, 41],
    iconAnchor: [12.5, 41],
    popupAnchor: [0, -41],
});

const myIconATM = L.icon({
    iconUrl:
        "data:image/png;base64,",
    iconSize: [25, 41],
    iconAnchor: [12.5, 41],
    popupAnchor: [0, -41],
});

class App extends React.Component {
    state = {
        location: {
            lat: 50,
            lng: 4,
        },
        zoom: 2,
        listATM: null,
    };

    componentDidMount() {
        navigator.geolocation.getCurrentPosition(position => {
            this.setState({
                location: {
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                },
                haveUsersLocation: true,
                zoom: 15,
            });
            fetch(
                "http://localhost:7778/atm?latitude=" +
                    this.state.location.lat +
                    "&longitude=" +
                    this.state.location.lng,
            )
                .then(response => response.json())
                .then(listATM => this.setState({listATM}));
        });
    }
    formSubmitted = event => {
        event.preventDefault();
    };

    renderMarker(listATM) {
        if (listATM != null) {
            console.log(listATM);
            return listATM.map((value, index) => (
                <Marker
                    key={value._id}
                    position={[value.latitude, value.longitude]}
                    icon={myIconATM}>
                    <Popup key={index}>{value.address}</Popup>
                </Marker>
            ));
        }
    }

    render() {
        const position = [this.state.location.lat, this.state.location.lng];
        const listATM = this.state.listATM;
        return (
            <div>
                <Map
                    className={"leaflet-map"}
                    center={position}
                    zoom={this.state.zoom}>
                    <TileLayer
                        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url={
                            "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                        }
                    />
                    <Marker position={position} icon={myIcon}>
                        <Popup>
                            You are here. <br /> Yes, news go fast these days...
                        </Popup>
                    </Marker>
                    {this.renderMarker(listATM)}
                </Map>
            </div>
        );
    }
}

ReactDOM.render(<App />, document.querySelector("#app"));
